/* eslint-disable no-param-reassign */
import * as React from 'react';
import { setAlignment } from '../actions';
import type { MobileToolbarButtonProps } from '@ali/4ever-component';

const MOBILE_TOOLBAR = {
  left: 'alignLeft',
  right: 'alignRight',
  center: 'alignCenter',
  justify: 'alignBoth',
};

function useDisabledAndClick(controller, type) {
  const handleClick = React.useCallback(
    () => controller.run('onAction', setAlignment(type)),
    [controller, type],
  );

  const { isCollapsedAtEdge } = controller.value.selection;
  const isDisabled = isCollapsedAtEdge;
  return { isDisabled, handleClick };
}

export default function createMobileHeading(locale: any) {
  return Object.keys(MOBILE_TOOLBAR).reduce((result, type) => {
    result[`${type}MobileBig`] = (props: MobileToolbarButtonProps) => {
      const { controller, Wrapper, item } = props;
      const isActive = controller.query('getAlignment') === type;
      // eslint-disable-next-line react-hooks/rules-of-hooks
      const { isDisabled, handleClick } = useDisabledAndClick(controller, type);
      const icon = MOBILE_TOOLBAR[type];

      return (
        <Wrapper
          item={item}
          type="big"
          isFocused={isActive}
          isDisabled={isDisabled}
          icon={icon}
          onClick={handleClick}
        />
      );
    };
    result[`${type}Mobile`] = (props: MobileToolbarButtonProps) => {
      const { controller, Wrapper, item } = props;
      const isActive = controller.query('getAlignment') === type;
      // eslint-disable-next-line react-hooks/rules-of-hooks
      const { isDisabled, handleClick } = useDisabledAndClick(controller, type);
      const icon = MOBILE_TOOLBAR[type];

      return (
        <Wrapper
          item={item}
          type="horiz"
          isActive={isActive}
          isDisabled={isDisabled}
          icon={icon}
          onClick={handleClick}
        />
      );
    };
    return result;
  }, {});
}
